.NavbarItems {
  background: linear-gradient(90deg, #0098E4 0%, #0783c2 100%);
  height: 80px;
  width: auto;
  display: flex;
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  /* overflow: hidden; */
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  z-index: 3;
  box-shadow: 0px 5px 10px -3px rgba(0,0,0,0.5);
}

.navbar-logo {
  justify-self: start;
  margin-left: 20px;
}

.logo {
  height: 60px;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(6,auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-links {
  color: white;
  font-size: 14px;
  text-decoration: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.nav-links:hover {
  background-color: #ca4a4a;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: fixed;
    height: 60px;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    position: absolute;
    overflow: hidden;
    top: 40px;
    left: -100%;
    padding-left: 0;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #0098E4;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 6;
  }

  .nav-links {
    text-align: center;
    padding: 1rem 0;
    width: 100%;
    display: table;
  }


  .nav-links:hover {
    background-color: #0783c2;
    border-radius: 0;
  }

  .navbar-logo {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.6rem;
    cursor: pointer;
  }
}
